.home-container {
    text-align: center;
    padding: 50px 20px;
    background-color: #f4f4f4;
  }
  
  .hero-section {
    background-color: #007bff;
    color: white;
    padding: 50px 20px;
    border-radius: 10px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  }
  
  .hero-section h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }
  
  .hero-section p {
    font-size: 1.2rem;
    margin-bottom: 20px;
  }
  
  .shop-now {
    background-color: white;
    color: #007bff;
    padding: 12px 20px;
    font-size: 18px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
  }
  
  .shop-now:hover {
    background-color: #0056b3;
    color: white;
  }
  
  .features {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 40px;
  }
  
  .feature-box {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
    width: 250px;
  }
  
  .feature-box h3 {
    color: #007bff;
    margin-bottom: 10px;
  }
  
  .feature-box p {
    color: #555;
  }
  .grid-container {
    display: flex;
    gap: 20px;
    justify-content: center;
  }
  
  .grid-item {
    position: relative;
    overflow: hidden;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  }
  
  .grid-item img {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    transition: transform 0.3s ease-in-out;
  }
  
  .grid-item p {
    text-align: center;
    font-weight: bold;
    margin-top: 8px;
    transition: color 0.3s ease-in-out;
  }
  
  /* Hover Effects */
  .grid-item:hover {
    transform: scale(1.1);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  }
  
  .grid-item:hover img {
    transform: rotate(5deg);
  }
  
  .grid-item:hover p {
    color: #ff5733;
  }
  
  /* Fade-in Animation */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .grid-item {
    animation: fadeIn 0.6s ease-in-out;
  }
  



  
/* Grid Styles */
.grid-container {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: nowrap; /* Prevents wrapping */
  overflow-x: auto; /* Scroll if too many items */
  padding: 10px;
  scrollbar-width: none; /* Hide scrollbar */
}

.grid-item {
  text-align: center;
  background: #fff;
  padding: 15px;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.5s ease-in-out, box-shadow 0.5s ease-in-out;
}

.grid-item img {
  width: 150px;
  height: 150px;
  border-radius: 10px;
  transition: transform 0.5s ease-in-out;
}

.grid-item:hover {
  transform: scale(1.1);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2);
}

.grid-item:hover img {
  transform: rotate(5deg);
}

.grid-item p {
  font-weight: bold;
  margin-top: 8px;
  transition: color 0.3s ease-in-out;
}

.grid-item:hover p {
  color: #ff5733;
}




/* Carousel Styles */
.carousel-container {
  width: 100%;
  overflow: hidden;
  position: relative;
  margin: 20px auto;
}

.carousel {
  display: flex;
  width: 200%; /* Double the images */
  animation: slide 30s infinite linear; /* Continuous sliding */
}

.carousel img {
  width: 25%; /* Adjust to fit original set of images */
  flex-shrink: 0;
  object-fit: cover;
}

/* Infinite Sliding Animation */
@keyframes slide {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(-50%); /* Moves only half, as images are duplicated */
  }
}



.container {
