.navbar {
  background: linear-gradient(135deg, #007bff, #0056b3);
  padding: 15px 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 12px;
  margin: 15px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease-in-out;
}

.logo {
  color: white;
  font-size: 24px;
  font-weight: bold;
  padding: 8px 15px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease-in-out;
}

.logo:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 15px;
}

.nav-links li {
  display: inline;
}

.nav-links a {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  text-decoration: none;
  font-size: 18px;
  font-weight: 500;
  padding: 10px 18px;
  border-radius: 8px;
  transition: all 0.3s ease-in-out;
}

.nav-links a:hover {
  background: rgb(0, 8, 160);
  color: #007bff;
  transform: translateY(-3px);
  box-shadow: 0px 4px 10px rgba(255, 255, 255, 0.3);
}
