/* Product Container */
.products-container {
  padding: 20px;
  animation: fadeIn 0.8s ease-in-out;
}

/* Filter Container */
.filter-container {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  animation: slideDown 0.8s ease-in-out;
}

/* Product Grid */
.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

/* Product Card with Fade-In Effect */
.product-card {
  background-color: rgb(255, 255, 255);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  animation: fadeIn 0.6s ease-in-out;
}

/* Hover Animation for Product Cards */
.product-card:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/* Modal with Smooth Slide-In */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transform: scale(0.8);
  animation: slideIn 0.5s ease-in-out forwards;
}

/* Modal Content */
.modal-content {
  background: rgb(255, 255, 255);
  padding: 20px;
  border-radius: 10px;
  position: relative;
  animation: fadeIn 0.5s ease-in-out;
}

/* Close Button Animation */
.modal-content .close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.modal-content .close:hover {
  transform: rotate(90deg);
}

/* Modal Input */
.modal-content input {
  width: 50px;
  margin-right: 10px;
}

/* Keyframe Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes slideDown {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
