.invoice-container {
    max-width: 800px;
    margin: 30px auto;
    padding: 20px;
    background: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .invoice-container h2 {
    color: #007bff;
    margin-bottom: 20px;
  }
  
  .invoice-content {
    text-align: left;
    margin-bottom: 20px;
  }
  
  .invoice-content h3 {
    color: #333;
    margin-bottom: 10px;
  }
  
  .invoice-content p {
    margin: 5px 0;
    color: #555;
  }
  
  .invoice-content ul {
    list-style: none;
    padding: 0;
  }
  
  .invoice-content li {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid #ddd;
  }
  
  .invoice-content li:last-child {
    border-bottom: none;
  }
  
  .invoice-container button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    margin: 10px;
  }
  
  .invoice-container button:hover {
    background-color: #0056b3;
  }